.box {
  padding: 10px;
  background-color: #fff;
  margin: 5px;
  border-radius: 4px;
}

.list {
  display: grid;
  justify-content: center;
  grid-template-columns: repeat(auto-fill, minmax(400px, 1fr));
  grid-gap: 24px;
  padding: 0;
  margin: 0;
  list-style: none;
}

.item {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 500px;
  padding: 20px;
  border: 1px solid #2a2a2a;
  border-radius: 4px;
  background-color: #d3b78e;
  box-shadow: 0px 2px 1px rgba(0, 0, 0, 0.2), 0px 1px 1px rgba(0, 0, 0, 0.14),
    0px 1px 3px rgba(0, 0, 0, 0.12);
}

.item::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 4px;

  background-color: #4caf50;

  transform: scaleX(0);
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.completed::before {
  transform: scaleX(1);
}

.text {
  margin-top: 0;
  margin-bottom: 0;
  margin-right: 24px;
  margin-left: 24px;
}

.completed .text {
  text-decoration: line-through;
}

.checkbox {
  cursor: pointer;
  width: 24px;
  height: 24px;
}
